.App {
  text-align: center;
}

@import url("https://fonts.googleapis.com/css2?family=Inconsolata:wght@500&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Inconsolata", monospace;
  /* background-color: aliceblue; */
}

body,
html {
  width: 100%;
  height: auto;
  background-color: aliceblue;
}

.icon-div {
  padding: 1rem;
  display: flex;
  justify-content: flex-end;
}

/* Timer CSS */

.timer-div {

  background-color: aliceblue;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}

.min-text {
  font-size: 20px;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
}

.time-text {
  margin: 1rem;
  font-size: 60px;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
}

.time-text-inner-div {
  padding: 2px;
  display: flex;
  justify-content: center;
}

.time-text-div {
  min-height: 300px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.start-btn,
.stop-btn,
.restart-btn {
  border: 1px solid lightblue;
  margin: 0 1rem;
}

/* category CSS  */
.category-btn-div {
  width: 100%;
  margin: auto 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 5px;
}

.category-btn {
  width: 110px;
  padding: 3px;
  border: 1px solid lightblue;
}

/* Style for the active category button */
.category-btn.active {
  background-color: #007bff;
}

/* Change to your desired background color





/* Settings CSS  */
.settings-div {
  width: 80%;
  background-color: aliceblue;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}

.input-field {
  width: 250px;
  background-color: aliceblue;
  color: black;
  border: 1px solid black;
  padding: 8px;
}

.settings-group {
  display: flex;
  flex-direction: column;
  margin: 1.5rem 1rem;
}

.save-btn {
  border: 1px solid lightblue;
}

.setting-text {
  font-size: 30px;
  font-weight: bold;
  margin: 1rem 0;
}